.bg-container {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    font-family: 'Times New Roman';
    margin: 15px;
    color: #ffffff;
  }

  .nav-bar{
    width: 95%;
    display: flex;
    align-items: center;
  }

  .predictButton{
    height: 50px;
    width: 120px;
    background-color: #2188f5;
    color: #ffffff;
    border: none;
    border-radius: 8px;
    margin-left: auto;
  }
  
  .bold-text {
    font-weight: bold;
  }
  
  .city {
    font-size: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .location-icon {
    color: Red;
    font-size: 40px;
    padding-top: 5px;
  }
  
  .city-drop {
    color: #ffffff;
    padding: 7px;
    border: none;
    background-color: transparent;
  }
  .city-drop:hover {
    border: 1px solid black;
    background-color: rgba(0, 0, 0, 0.47);
  }
  
  .city-drop option {
    background-color: transparent;
  }
  
  .table {
    width: 96vw;
    margin-top: 5px;
    margin-bottom: 20px;
    color: #ffffff;
    font-size: 25px;
  }
  
  .table td,
  .table th {
    padding: 15px;
    vertical-align: top;
    border-top: 1px solid #dee2e6;
    max-width: 50px;
    text-align: center;
    color: #ffffff;
  }
  
  .table tr {
    margin: 20px;
  }
  
  .clock-icon {
    color: #ffffff;
    font-size: 25px;
    margin-right: 5px;
  }
  
  .time-container {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 21px;
  }
  
  .bg-danger-color {
    background-color: rgba(255, 0, 0, 0.3);
  }

  .graph-container{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
    color: #ffffff;
    flex-wrap: wrap;
    width: 98%;
    padding: 10px;
    text-align: center;
  }

  .graph{
    width: 70vw;
    background-color: white;
    margin: 5px;
  }

  .bg-good{
    background-color: #00c936;
  }

.trends{
  color: #ffffff;
  text-align: center;
}


  /* index.css */

.graph-selector {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.graph-selector label {
  font-size: 28px;
  margin-right: 20px;
  cursor: pointer;
  color: #fff; /* Set label text color to white */
}

.graph-selector input[type="radio"] {
  display: none; /* Hide the actual radio button */
}

.graph-selector input[type="radio"] + label {
  position: relative;
  padding-left: 25px; /* Adjust based on your design */
  font-size: 16px;
  line-height: 20px;
}

.graph-selector input[type="radio"] + label:before {
  content: '';
  position: absolute;
  left: 0;
  top: 1px;
  width: 18px;
  height: 18px;
  border: 1px solid #ccc;
  border-radius: 50%;
  background-color: transparent; /* Transparent background */
}


.graph-selector input[type="radio"] + label:after {
  content: '';
  position: absolute;
  display: none;
}

.graph-selector input[type="radio"]:checked + label:after {
  display: block;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: #fff;
  top: 5px;
  left: 5px;
}

.selected{
  /* text-decoration: underline; */
  background-color: #2188f5;
  border-radius: 8px;
  padding: 4px;
}